import * as React from "react"
import Navbar from "../components/navigation.js"
import "../styles/style.css"


// children: whatever is inside of the tags
// other props are explicitly named
// pageTitle: one for the entire page
// contentTitle: 
export default function Layout(props){
    return (
        <>
        <Navbar />
        <div id="page" className="p-2 min-h-screen max-h-auto bg-gradient-to-br from-rose-100  to-indigo-300">

            <PageTitle title= {props.pageTitle? props.pageTitle : "AddPageTitle!!!"} />

            <div id="content_box" className="flex justify-center w-3/5 m-auto p-1">
                <div>{props.children}</div>
            </div>       
            </div>
        </>
    )
}

function PageTitle( {title} ) {
    return (
        <div className="font-serif text-3xl text-gray-700 text-center p-8">{title}</div>
    )
}

// the blocks in which page content is displayed
export function Content( props ) {
    const contentTitle = props.contentTitle ? props.contentTitle : "";
    const contentBody  = props.children ? props.children : "please add content";
    
    return (
        <div id={props.contentTitle} className="p-2 m-1 bg-gray-200 border-2 rounded-lg border-white">
            <div className="text-lg text-indigo-900">
                {contentTitle}
            </div>
            <div className=" p-1 text-base">
                {contentBody}
            </div>
        </div>        
    )
}



export function ImageGallery({images}){
    return (
      <div class="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8">
        {
            images.map((image) => {
            return(<div><Thumbnail image={image}/></div>)
        })
    }
      </div>
    )
}

export function VideoGallery({videos}){
    return (
        <div class="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8">
      {
          videos.map((video) => {
              return(<div><VideoThumbnail video={video}/></div>)
        })
    }
    </div>
  )
}


export function Thumbnail({image}) {
    return (
        <div class="h-60 p-4 border-1 border-rose-900 bg-neutral-50">
            <a href={image.src}>
            <img
            class="h-full w-full object-cover"
            src={image.src} 
            title={image.title? image.title : "thumbnail"} 
            alt={image.description?image.description:"thumbnail"}/>
            </a>
        </div>
    )
}


export function VideoThumbnail({video}) {
  return (
      <div class="h-60 p-4 border-rose-900 bg-neutral-50 border-2">
          
          <video controls>

          <source
            class="h-full w-full object-cover"
            src={video.src} 
            title={video.title? video.title : "thumbnail"} 
            alt={video.description?video.description:"thumbnail"}/>
            Video Unsupported
          </video>
          
      </div>
  )
}


