import * as React from "react"
import Layout, {Content, ImageGallery} from "../components/layout.js"
import images from "../components/images2D.js"

export default function IndexPage() {
  return (
    <main>
      <Layout pageTitle="Digital Art">
        <Content >
          <ImageGallery images={images}/>
        </Content>
      </Layout>     
    </main>
  )
}

export const Head = () => <title>Simone Hamby</title>
