/* 
    i put navigation elements on a separate page
    to make it a little more organized if i update the links in the future
*/

import * as React from "react"
import { homeImages } from "../components/images2D"
export const siteName="Simone Hamby"


export default function Navbar () {
    return (
        <div id="Navbar" className="flex gap-4 p-10 border-b-1 border-b-gray-700 bg-rose-300">
        <div className="flex-1 text-gray-50 hover:text-rose-100 text-4xl font-semibold">
            <a href="/">{siteName}</a>
        </div>
        <LinkButtons/>
        </div>   
  )
}

export function LinkButtons() {
    const containerStyle="flex gap-4 text-lg text-center items-center max-md:hidden" ;
    const linkStyle="w-30 h-10 content-center bg-gray-200 rounded-md flex-none border-white hover:bg-indigo-300";
    
    return(
        <div className={containerStyle}>
        <a className={linkStyle} href="/digital_gallery"               >Gallery</a>
        <a className={linkStyle} href="/about"                         >About</a>
        {/* <a className={linkStyle} href="/contact"                       >Contact</a> */}
        <a className={linkStyle} href="https://www.github.com/cjhamby" >Github</a>
        {/* <a className={linkStyle} href="/tailwind_playground"           >Secret</a> */}
        </div>
    )
}

export function LinkCards () {
    
    const tempClass="flex-shrink-0 w-full h-full lg:basis-2/5 2xl:basis-1/5 overflow-hidden";
    
    return(
        <div class="flex flex-wrap md:flex-row justify-center gap-8">
            <div class={tempClass}><LinkCard image={homeImages.get("digital_card")} myRef="digital_gallery" title="Digital Art"/> </div>
            <div class={tempClass}><LinkCard image={homeImages.get("render_card")}  myRef="render_gallery" title="3D Models"/> </div>
            <div class={tempClass}><LinkCard image={homeImages.get("anim_card")}    myRef="animation_gallery" title="Animation"/> </div>
            <div class={tempClass}><LinkCard image={homeImages.get("about_card")}   myRef="about" title="About Me"/> </div>
        </div>
    )
}

// cards displayed on the index page
// with links to other parts of the site
export function LinkCard({image, title, myRef}) {
    return(
        //<div class="h-50 border-2 border-white text-black text-center bg-gray-200  hover:bg-rose-200">
        <div class="h-60 border-2 border-white rounded-sm text-black text-center bg-gray-200  hover:bg-gradient-to-b hover:from-gray-200 hover:to-rose-300">

        <a href={myRef}>
            <div className="text-lg ">{title}</div>
            <img class="h-4/5 w-4/5 object-cover m-auto" title={image.title} src={image.src} alt={image.description?image.description:"cardimage"} />
        </a>
        </div>
    )
}
